import React from 'react';

const LoginForm = ({ handleLogin, handleUsernameInput, handlePasswordInput, username, password }) => {
  return (
    <>
      <form onSubmit={handleLogin}>
        <div>
          Username
          <input type='text' value={username} name='Username' onChange={handleUsernameInput} />
        </div>
        <div>
          Password
          <input type='text' value={password} name='Password' onChange={handlePasswordInput} />
        </div>
        <button type='submit'>Submit</button>
      </form>
    </>
  )
}

export default LoginForm;