import React from 'react';
import contactsService from '../services/contacts'

const Contact = ({ contact, setContacts, allContacts, setError }) => {
  const deleteHandler = (e) => {
    if (!window.confirm(`Are you sure you want to delete ${contact.firstName} ${contact.lastName} from your contacts?`)) return;
    contactsService.remove(contact.id)
                   .then(() => {
                    setContacts(allContacts.filter(p => p.id !== contact.id));
                   })
                   .catch(err => {
                    setError(err);
                    setTimeout(setError.bind(null, null), 4000);
                  });
  }

  const updateHandler = (e) => {
    
  }

  return (
    <div className='contact'>
      <h3>{contact.firstName}{'\u00a0'}{contact.lastName}: {contact.phoneNumber}</h3>
      <button onClick={deleteHandler}>Delete</button>
      <button onClick={updateHandler}>Update</button>
    </div>
  );
}

export default Contact