import React from 'react';
import { useState, useEffect } from 'react';
import contactsService from './services/contacts';
import loginService from './services/login';
import './App.css';
import ContactDisplay from './components/ContactDisplay';
import ErrorDisplay from './components/ErrorDisplay';
import LoginForm from './components/LoginForm';
import ContactForm from './components/ContactForm';
import Togglable from './components/Togglable';
import LoginInfo from './components/LoginInfo';
import Search from './components/Search';

function App() {
  const [contacts, setContacts] = useState([]);
  const [newFirstName, setNewFirstName] = useState('');
  const [newLastName, setNewLastName] = useState('');
  const [newPhoneNumber, setNewPhoneNumber] = useState('');
  const [filterBy, setFilterBy] = useState('');
  const [error, setError] = useState(null);
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [loginVisible, setLoginVisible] = useState(false);
  const [user, setUser] = useState(null);

  useEffect(() => {
    contactsService.getAll()
                   .then(contacts => setContacts(contacts));
  }, []);

  useEffect(() => {
    const loggedInUserJSON = window.localStorage.getItem('loggedInContactsUser');
    if (loggedInUserJSON) {
      const user = JSON.parse(loggedInUserJSON);
      setUser(user);
      contactsService.setToken(user.token);
    }
  }, []);

  const alreadyExists = (newContact) => {
    return (contacts.some(contact => {
             return ( contact.firstName.toLowerCase() === newContact.firstName.toLowerCase() &&
                      contact.lastName.toLowerCase() === newContact.lastName.toLowerCase() )
    }));
  }

  const handleSubmit = (event) => {
    event.preventDefault();

    let newContact = {
      firstName: newFirstName,
      lastName: newLastName,
      phoneNumber: newPhoneNumber,
    }

    if (alreadyExists(newContact)) {
      return alert(`"${newFirstName} ${newLastName}" already exists!`);
    }

    contactsService.create(newContact)
                   .then(contact => {
                       setContacts(contacts.concat(contact));
                       setNewFirstName('');
                       setNewLastName('');
                       setNewPhoneNumber('');
                   })
                  .catch(err => {
                    setError(err);
                    setTimeout(setError.bind(null, null), 4000);
                  });
  }

  const handleFirstNameInput = (event) => {
    let newFirstName = event.target.value.trim();
    setNewFirstName(newFirstName);
  }

  const handleLastNameInput = (event) => {
    let newLastName = event.target.value.trim();
    setNewLastName(newLastName);
  }

  const handlePhoneNumberInput = (event) => {
    let newPhoneNumber = event.target.value;
    setNewPhoneNumber(newPhoneNumber);
  }

  const handleSearchInput = (event) => {
    let query = event.target.value;
    setFilterBy(query);
  }

  let contactsToShow = contacts.filter(contact => {
    return `${contact.firstName.toLowerCase()} ${contact.lastName.toLowerCase()}`.includes(filterBy.toLocaleLowerCase())
  });

  const handleLogin = async (e) => {
    e.preventDefault();
    try {
      const user = await loginService.login({ username, password });
      window.localStorage.setItem('loggedInContactsUser', JSON.stringify(user));

      contactsService.setToken(user.token);
      setUser(user);
      setUsername('');
      setPassword('');
    } catch (err) {
      setError(err);
      setTimeout(() => {
        setError(null)
      }, 5000)
    }
  }

  const handleUsernameInput = (e) => {
    setUsername(e.target.value);
  }

  const handlePasswordInput = (e) => {
    setPassword(e.target.value);
  }

  const handleLogout = (e) => {
    window.localStorage.removeItem('loggedInContactsUser');
    setUser(null);
  }

  return (
    <div className="App">
      <h1>Contacts</h1>
      <ErrorDisplay error={error}></ErrorDisplay>
      {
      user === null ? 
      <Togglable buttonLabel='Login'>
        <LoginForm handleLogin={handleLogin} handleUsernameInput={handleUsernameInput} handlePasswordInput={handlePasswordInput} username={username} password={password} loginVisible={loginVisible} setLoginVisible={setLoginVisible}></LoginForm>
      </Togglable>  : 
      <>
        <LoginInfo user={user} handleLogout={handleLogout}></LoginInfo>
        <Togglable buttonLabel='Add Contact'>
          <ContactForm handleSubmit={handleSubmit} handleFirstNameInput={handleFirstNameInput} handleLastNameInput={handleLastNameInput} handlePhoneNumberInput={handlePhoneNumberInput} newFirstName={newFirstName} newLastName={newLastName} newPhoneNumber={newPhoneNumber}></ContactForm>
        </Togglable>
        <ContactDisplay allContacts={contacts} contactsToShow={contactsToShow} setContacts={setContacts} error={error} setError={setError}>
          <Search handleSearchInput={handleSearchInput} value={filterBy}></Search>
        </ContactDisplay>
      </>
      }
    </div>
  );
}

export default App;
